import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { IconButton, TablePagination } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';

const columns = [
	{ id: 'folio', label: 'Folio', minWidth: 20, align: 'center' },
	{ id: 'cliente', label: 'Cliente', minWidth: 330 },
	{ id: 'fecha', label: 'Fecha', minWidth: 100, align: 'center'},
	{ id: 'total', label: 'Total', minWidth: 100, align: 'center'}
];

function createData(folio, cliente, fecha, total) {
	return { folio, cliente, fecha, total};
}

const rows = [
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10),
	createData(2, 'Jabon', 50, 10)
];

const useStyles = makeStyles({
	root: {
		width: '100%'
	},
	container: {
		maxHeight: '65vh'
	}
});

export default function CotizacionesPendientes({ handleClickOpen }) {
	const classes = useStyles();
	const [ page, setPage ] = React.useState(0);
	const [ rowsPerPage, setRowsPerPage ] = React.useState(10);

	const handleChangePage = (event, newPage) => {
		setPage(newPage);
	};

	const handleChangeRowsPerPage = (event) => {
		setRowsPerPage(+event.target.value);
		setPage(0);
	};

	return (
		<Paper className={classes.root}>
			<TableContainer className={classes.container}>
				<Table stickyHeader size="small" aria-label="a dense table">
					<TableHead>
						<TableRow>
							{columns.map((column) => (
								<TableCell key={column.id} align={column.align} style={{ width: column.minWidth }}>
									{column.label}
								</TableCell>
							))}
							<TableCell align='center' style={{ width: 35 }}>
								Retomar
							</TableCell>
							<TableCell align='center' style={{ width: 35 }}>
								Eliminar
							</TableCell>
						</TableRow>
					</TableHead>
					<TableBody>
						{rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
							return (
								<CotizacionesRows row={row} />
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			<TablePagination
				rowsPerPageOptions={[ 10, 25, 100 ]}
				component="div"
				count={rows.length}
				rowsPerPage={rowsPerPage}
				page={page}
				onChangePage={handleChangePage}
				onChangeRowsPerPage={handleChangeRowsPerPage}
			/>
		</Paper>
	);
};


const CotizacionesRows = ({row}) => {

	return(
		<>
			<TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
				<TableCell align='center' >
					uno
				</TableCell>
				<TableCell >
					uno
				</TableCell>
				<TableCell align='center' >
					uno
				</TableCell>
				<TableCell align='center' >
					uno
				</TableCell>
				<TableCell align='center' >
					<IconButton aria-label="delete" size='small'>
						<DeleteIcon fontSize="small" />
					</IconButton>
				</TableCell>
				<TableCell align='center' >
					<IconButton aria-label="delete" size='small'>
						<DeleteIcon fontSize="small" />
					</IconButton>
				</TableCell>
			</TableRow>
		</>
	);

}
